.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.7;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1{
    font-size: 4rem;
    padding:0.6rem 0 1.5rem ;
}

.hero .content h1 p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.subText{
    padding-bottom: 3rem;
}

.hero .btn{
    margin: 1rem 0.5rem;
}

@media screen and (max-width: 666px) {
    .hero .content h1{
        font-size: 3rem;
    }

    .hero .content{
        position: absolute;
        transform: translate(-50%, -50%);
        top: 55%;
        left: 50%;
        text-align: center;
    }

    .hero .content p{
        font-size: 1.4rem;
    }

    .buttons{
        display: flex;
        flex-direction: column;
    }

    .hero .btn{
        padding: 10px 0px;
        font-weight: 600;
      }

      .hero .btn{
        margin: 0.6rem 0.5rem;
    }
}